<template>
<div>
  <b-card header="" >
    <div>(Z): {{ cameraTheta.toFixed(1) }}
      <b-form-input type="range" id="cameraTheta" :value="cameraTheta" 
        min="0" max="180" @change="cameraThetaChanged($event)"/>
    </div>
    <div>(X): {{ cameraPhi.toFixed(1) }}
      <b-form-input type="range" id="cameraPhi" :value="cameraPhi" 
      min="0" max="360" @change="cameraPhiChanged($event)" />
    </div>
    <div>: {{cameraDis}}
      <b-form-input type="range" id="cameraDis" :value="cameraDis" 
        min="1" max="180" @change="cameraDisChanged($event)" />
    </div>
  </b-card>
  <b-card header=""> 
    <div>: {{sphere.size}}
      <b-form-input type="range" id="sphereRadius" :value="sphere.size" min="1" max="10" @change="sphereRadiusChanged($event)" />
    </div>
    <b-form-group label="">
      <b-radio-group v-model="sphere.drawMode" class="sphereMode">
        <div><b-radio :value="0">Surface</b-radio></div>
        <div><b-radio :value="1">Line</b-radio></div>
        <div><b-radio :value="2">lessLine</b-radio></div>
      </b-radio-group>
    </b-form-group>
  </b-card>
  <b-card header=""> 
    <div class="option">
      <b-checkbox></b-checkbox>
      <b-checkbox></b-checkbox>
      <b-checkbox></b-checkbox>
      <b-checkbox></b-checkbox>
    </div>
    <div class="action">
      <b-btn></b-btn>
      <b-btn></b-btn>
      <b-btn></b-btn>
      <b-btn></b-btn>
    </div>
    <div>: {{sphereAlpha}}
      <b-form-input type="range" id="sphereOpacity" :value="sphereAlpha" min="0" max="100" 
        @change="sphere.alpha = parseFloat($event) / 100" />
    </div>
    <div>: {{indicatorSize.toFixed(1)}}
      <b-form-input type="range" id="indicatorSize" :value="indicatorSize" min="1" max="100" 
        @change="setIndicatorSize($event)" />
    </div>
    <div> {{pathWidth}}
      <b-form-input type="range" id="pathWidth" :value="pathWidth" min="1" max="100" 
        @change="setPathWidth($event)" />
    </div>
    <div class="hidden">
      <b-form-input type="range" id="pathWidth" :value="pathWidth" min="1" max="100" 
        @change="setPathWidth($event)" />
    </div>
  </b-card>



</div>
</template>

<script>
function radToDeg(rad) {
  return rad * 180 / Math.PI;
}
export default {
  data() {
    return {
      camera: {
        theta: 0,
        phi: 0,
        dis: 4
      },
      sphere: {
        size: 4,
        alpha: 1,
        drawMode: 0,
      },
      indicator: {
        size: 1
      },
      sensorPath: {
        size: 1
      }
    }
  },
  computed: {
    cameraPhi() {
      return radToDeg(this.camera.phi);
    }, 
    cameraTheta() {
      return radToDeg(this.camera.theta);
    },
    cameraDis() {
      return this.camera.dis;
    },
    sphereAlpha() {
      return this.sphere.alpha * 100;
    },
    indicatorSize() {
      return this.indicator.size * 100;
    },
    pathWidth() {
      return this.sensorPath.size;
    }
  },
  methods: {
    cameraPhiChanged(value) {
      this.camera.rotate(null, value, null);
    },
    cameraThetaChanged(value) {
      this.camera.rotate(value, null, null);
    },
    cameraDisChanged(value) {
      this.camera.rotate(null, null, value);
    },
    sphereRadiusChanged(value) {
      this.sphere.setSize(value);
      // this.camera.rotate()
    },
    setIndicatorSize(value) {
      this.indicator.setScale(parseFloat(value) / 100);
    },
    setPathWidth(value) {
      this.sensorPath.setSize(value);
    },
    init(spacePath) {
      this.spacePath = spacePath;
      console.log(spacePath)
      this.camera = spacePath.camera;
      this.sphere = spacePath.sphere;
      this.indicator = spacePath.sensorPoint;
      this.sensorPath = spacePath.sensorPath;
      console.log(this.sphere)
    }
  },
  mounted() {
    console.log("Sidebar Mounted")
  }
}
</script>

<style>

</style>
